// Specialisation for the MediaCapturer API surface
module.exports = function makeMediaCapturerProxy(mediaCapturer) {
  // eslint-disable-next-line no-param-reassign
  mediaCapturer.selectSources = (...args) => mediaCapturer._.selectSources(...args);
  mediaCapturer.listenForDeviceChanges();
  return mediaCapturer;
};
